html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Lato", sans-serif;
  font-size: 1rem; }

.contenedor-poke {
  max-width: 1080px;
  margin: 0 auto;
  background-color: #B2FBFD; }

a {
  text-decoration: none; }

img {
  display: block;
  margin: 0 auto;
  max-width: 100%; }

p, h1, h2, h3 {
  text-align: center; }

.contenedor-poke {
  display: none !important;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-items: center; }
  .contenedor-poke .explorador {
    background-color: #1B8DD6;
    border-radius: 1rem;
    border: solid 3px #E4E5DD;
    position: relative;
    width: 95%;
    padding-bottom: .5rem; }
    .contenedor-poke .explorador.seleccionado {
      border: solid 3px #f93e3e; }
  .contenedor-poke .explorador:after {
    content: "";
    display: block; }
  .contenedor-poke .nombre-pokemon {
    width: 100%;
    margin: 0;
    color: #F4F4F4;
    font-weight: 400;
    font-size: 2rem; }

.imagen-header {
  position: relative; }
  .imagen-header .overlay {
    position: absolute;
    width: 100%;
    height: 100%; }

.botones {
  display: flex;
  justify-content: center;
  align-items: end;
  height: 95%;
  width: 100%;
  gap: .5rem;
  position: relative; }
  .botones .nav-top {
    border-radius: 100%;
    padding: .8rem;
    background-color: #3a3a3a;
    color: #F4F4F4;
    border: none; }
    .botones .nav-top:hover {
      cursor: pointer; }

.imagen-footer {
  position: relative; }
  .imagen-footer .overlay {
    position: absolute;
    width: 100%;
    height: 100%; }

.botones-footer {
  display: flex;
  justify-content: center;
  align-items: start;
  width: 100%;
  gap: .5rem;
  position: relative;
  margin-top: 1rem; }
  .botones-footer .nav-bot, .botones-footer .cerrar {
    border-radius: 100%;
    padding: .8rem;
    background-color: #3a3a3a;
    color: #F4F4F4;
    border: none; }
    .botones-footer .nav-bot:hover, .botones-footer .cerrar:hover {
      cursor: pointer; }

.abrir {
  display: flex;
  background-color: #B2FBFD;
  margin: 0 auto;
  justify-content: center;
  width: 1080px;
  max-width: 100%; }
  .abrir button {
    text-align: center;
    height: 12vh;
    width: 12vh;
    border-radius: 100%;
    border: none;
    background-color: #1B8DD6; }
    .abrir button:hover {
      cursor: pointer; }
